import React, { useState } from "react";
import style from "../LibraryMembers.module.css";
import { useInView } from "react-intersection-observer";
import { useEffect } from "react";
import {
  useGetStudentLibraryTime,
  useLibraryGetStudentInOutHistory,
} from "../../../../../../../hooks/member_tab/library-api";
import QvipleLoading from "../../../../../../../Loader/QvipleLoading";
import InOutHistoryCard from "./InOutHistoryCard";
import { useTranslation } from "react-i18next";
import BorderBottom from "../../../../../../../StaffTab/components/DividerTab/MainContent/StaffAProfile/BorderBottom";

function InOutHistory({ mid, isStudent }) {
  const { t } = useTranslation();
  const [ref, inView] = useInView();
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [showingDataLoading, setShowingDataLoading] = useState(true);
  const [historyBookList, setHistoryBookList] = useState({
    memberId: "",
    historyList: [],
  });

  const {
    libraryGetStudentInOutHistory,
    libraryGetStudentInOutHistoryRefetch,
  } = useLibraryGetStudentInOutHistory({
    data: {
      sid: mid,
      page: historyBookList.memberId !== mid ? 1 : page,
      limit: 10,
    },
    skip: !mid,
  });
  const { getStudentLibraryTime, getStudentLibraryTimeRefetch } =
    useGetStudentLibraryTime({
      data: {
        sid: mid,
      },
      skip: !mid,
    });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);

  useEffect(() => {
    if (mid) {
      getStudentLibraryTimeRefetch();
    }
  }, [mid, getStudentLibraryTimeRefetch]);

  useEffect(() => {
    if (mid) {
      setShowingDataLoading(true);
      libraryGetStudentInOutHistoryRefetch();
    }
  }, [page, mid, libraryGetStudentInOutHistoryRefetch]);

  useEffect(() => {
    if (libraryGetStudentInOutHistory?.history) {
      if (historyBookList.memberId === mid) {
        setHistoryBookList((prevState) => ({
          ...prevState,
          historyList: [
            ...new Set(
              [
                ...prevState.historyList,
                ...libraryGetStudentInOutHistory?.history,
              ]?.map(JSON.stringify)
            ),
          ]?.map(JSON.parse),
        }));
        setShowingDataLoading(false);
      } else {
        setHistoryBookList({
          memberId: mid,
          historyList: [...libraryGetStudentInOutHistory?.history],
        });
        setShowingDataLoading(false);
      }
    } else if (libraryGetStudentInOutHistory?.history?.length === 0)
      setShowingDataLoading(false);
    else {
    }
    if (libraryGetStudentInOutHistory?.history?.length === 10) setState(true);
    else setState(false);
  }, [libraryGetStudentInOutHistory?.history]);
  // console.info("s.fms gsd", historyBookList);
  return (
    <div
      className={
        isStudent ? style.membersContainerStudent : style.membersContainer
      }
    >
      {isStudent && (
        <>
          <h6>{t("logs")}</h6>
          <BorderBottom
            customStyle={{
              width: "100%",
              marginBottom: "0.5rem",
            }}
          />
        </>
      )}

      <h6
        style={{
          marginBottom: isStudent ? "1rem" : "1rem",
        }}
      >
        {t("total_library_time")}{" "}
        {`${getStudentLibraryTime?.student?.library_total_time_spent?.hours}:${getStudentLibraryTime?.student?.library_total_time_spent?.minutes}:${getStudentLibraryTime?.student?.library_total_time_spent?.seconds}`}{" "}
        {` hr`}
      </h6>
      {historyBookList.historyList?.map((history, index) =>
        historyBookList.historyList?.length === index + 1 ? (
          <div
            key={history?._id}
            ref={ref}
            style={{
              width: "100%",
            }}
          >
            <InOutHistoryCard history={history} />
          </div>
        ) : (
          <InOutHistoryCard history={history} key={history?._id} />
        )
      )}
      {showingDataLoading && <QvipleLoading />}
      {!showingDataLoading && historyBookList.historyList?.length === 0 && (
        <div className={style.noData}>
          <img src="/images/nodata.jpg" alt="" />
        </div>
      )}
    </div>
  );
}

export default InOutHistory;
