import moment from "moment";
import style from "../../LibraryFunctions.module.css";
import { useTranslation } from "react-i18next";
const InOutHistoryCard = ({ history }) => {
  const { t } = useTranslation();
  return (
    <div className={style.iohc_card}>
      <img
        src="/images/member_tab/subject/subject-daily-update-icon.svg"
        alt="icons"
      />
      <div className={style.iohc_card_container}>
        <div className={style.iohc_card_inner}>
          <h6>{t("in_time")}</h6>
          <p>
            {moment(history?.created_at).format("DD/MM/YYYY")}
            {", "}
            {/* {moment(history?.created_at).format("hh:mm:ss a")} */}
            {history?.in_time}
          </p>
        </div>
        {history?.is_valid === "Yes" ? (
          <>
            <div className={style.iohc_card_inner}>
              <h6>{t("out_time")}</h6>
              <p>
                {moment(history?.created_at).format("DD/MM/YYYY")}
                {", "}
                {history?.out_time}
              </p>
            </div>
            <div className={style.iohc_card_inner}>
              <h6>{t("total_time")}</h6>
              <p>
                {history?.total_spent_time ?? ""} {" hr"}
              </p>
            </div>
          </>
        ) : null}
      </div>
    </div>
  );
};

export default InOutHistoryCard;
